import React from "react";
import ReactGa, { OutboundLink } from "react-ga";
import { Container, Row, Col } from "react-bootstrap";
import Cookies from "universal-cookie";
import { Link } from "react-router-dom";

const Header = (props) => {
  const fullName = props.user
    ? `${props.user.first_name} ${props.user.last_name}`
    : "";

  const logout = async () => {
    const request = new Request(`/api/v1/auth/logout`, {
      body: JSON.stringify(),
      headers: {
        Accept: "application/json",
      },
      method: "POST",
    });
    await fetch(request);

    const cookies = new Cookies();
    cookies.set("user", null, { path: "/" });

    ReactGa.event({
      category: "User",
      action: "Logged Out",
    });
    ReactGa.set({ userId: null });

    window.location.href = "/login";
  };

  return (
    <Container
      component="header"
      fluid
      id="collection-5d838b677bb21d4a550fe7d2"
      style={{
        backgroundColor: "#1e1e1e",
        color: "white",
        marginBottom: "35px",
      }}
    >
      <Container>
        <Row>
          <Col xs={1}>
            <img
              alt="Z_Circle"
              src={require("./assets/Z Circle_Transparent.png")}
              style={{ height: "60px", margin: "9px 0px" }}
            />
          </Col>

          <Col xs={7} style={{ paddingTop: "5px" }}>
            <div style={{ fontFamily: "Book Antiqua", fontSize: "10px" }}>
              Welcome,
            </div>
            <div style={{ fontFamily: "Baskerville", fontSize: "36px" }}>
              <span style={{ color: "#00AC4D" }}>{fullName}</span>, to Zyyo's
              Investor Portal
            </div>
          </Col>

          <Col xs={4}>
            <Container
              style={{
                fontFamily: `Lato, "Lucida Grande", Tahoma, sans-serif`,
                fontSize: "12px",
              }}
            >
              <Row>
                <Col>
                  <Link
                    to="/marketplace"
                    style={{
                      color: "white",
                      display: "inline-block",
                      margin: "30px 0px",
                    }}
                  >
                    MARKETPLACE
                  </Link>
                </Col>
                <Col>
                  <OutboundLink
                    to="mailto:info@zyyo.com"
                    eventLabel="contact"
                    style={{
                      color: "white",
                      display: "inline-block",
                      margin: "30px 0px",
                    }}
                    target="_blank"
                  >
                    CONTACT
                  </OutboundLink>
                </Col>
                <Col>
                  <Link
                    onClick={logout}
                    style={{
                      backgroundColor: "#0F6536",
                      color: "white",
                      display: "inline-block",
                      fontWeight: "bold",
                      padding: "8px 12px",
                      margin: "22px 0",
                    }}
                    to="#"
                  >
                    LOG OUT
                  </Link>
                </Col>
              </Row>
            </Container>
          </Col>
        </Row>
      </Container>
    </Container>
  );
};

export default Header;
