import React, { useEffect, useState } from "react";
import ReactGa from "react-ga";
import { Container, Row } from "reactstrap";
import Cookies from "universal-cookie";

function Login() {
  const [values, setValues] = useState({ email: "", password: "" });

  useEffect(() => {
    ReactGa.pageview(window.location.pathname);
  }, []);

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    setValues({ ...values, submitting: true, error: "" });

    const request = new Request(`/api/v1/auth/login`, {
      body: JSON.stringify({
        email: values.email,
        password: values.password,
      }),
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "POST",
    });
    const response = await fetch(request);

    if (response.status === 401) {
      setValues({
        ...values,
        error: "Invalid username or password",
        submitting: false,
      });
    } else if (response.ok) {
      const user = await response.json();
      if (user && user.id) {
        const cookies = new Cookies();
        cookies.set("user", user, { path: "/" });
        ReactGa.set({ userId: user.id });
        window.location.href = "/marketplace";
      } else {
        // error
      }
    }
  };

  return (
    <center>
      <div
        style={{
          position: "absolute",
          left: "50%",
          top: "50%",
          transform: "translate(-50%, -50%)",
          backgroundColor: "rgb(26,26,26)",
          width: "500px",
        }}
      >
        <img
          alt="Zyyo logo"
          src={require("./assets/Zyyo_Logo_Export.png")}
          style={{ width: "50%", marginBottom: "30px" }}
        />
        <form method="post" onSubmit={handleSubmit}>
          <Container>
            <Row
              style={{
                display: "flex",
                justifyContent: "center",
                marginBottom: "25px",
              }}
            >
              <input
                disabled={values.submitting}
                name="email"
                onChange={handleChange("email")}
                placeholder="Email"
                required={true}
                style={{
                  backgroundColor: "#212121",
                  paddingLeft: "20px",
                  width: "70%",
                  height: "50px",
                  border: "white 1px none",
                  borderRadius: "50px",
                  color: "white",
                }}
                type="email"
                value={values.email}
              />
            </Row>
            <Row
              style={{
                display: "flex",
                justifyContent: "center",
                marginBottom: "25px",
              }}
            >
              <input
                disabled={values.submitting}
                name="password"
                onChange={handleChange("password")}
                placeholder="Password"
                required={true}
                style={{
                  backgroundColor: "#212121",
                  paddingLeft: "20px",
                  width: "70%",
                  height: "50px",
                  border: "white 1px none",
                  borderRadius: "50px",
                  color: "white",
                }}
                type="password"
                value={values.password}
              />
            </Row>
            <Row
              style={{
                color: "red",
                display: "flex",
                justifyContent: "center",
                marginBottom: "25px",
              }}
            >
              {values.error}
            </Row>
            <Row
              style={{
                display: "flex",
                justifyContent: "center",
              }}
            >
              <button
                type="submit"
                style={{
                  backgroundColor: "rgb(39,173,88)",
                  color: "white",
                  borderStyle: "none",
                  width: "38%",
                  height: "50px",
                  borderRadius: "80px",
                  marginBottom: "20px",
                }}
              >
                Sign In
              </button>
            </Row>
            <Row
              style={{
                display: "flex",
                justifyContent: "center",
              }}
            >
              <button
                type="submit"
                style={{
                  backgroundColor: "rgb(26,26,26)",
                  color: "rgb(117,117,117)",
                  borderStyle: "none",
                  width: "38%",
                  borderRadius: "80px",
                  marginBottom: "20px",
                }}
              >
                Forgot Password?
              </button>
            </Row>
          </Container>
        </form>
      </div>
    </center>
  );
}

export default Login;
