function Marketplace() {
  const [deals, setDeals] = useState([]);

  const fetchDeals = async () => {
    const request = new Request(`/api/v1/deals`, {
      headers: {
        Accept: "application/json",
      },
      method: "GET",
    });
    const response = await fetch(request);

    if (response.status === 401) {
      window.location.href = "/login";
    } else if (response.ok) {
      const deals = await response.json();
      setDeals(deals);
    }
  };

  useEffect(() => {
    ReactGa.pageview(window.location.pathname);
    fetchDeals();
  }, []);

  const dealPreviews = deals.map((deal) => (
    <Col
      key={deal.sfid}
      sm={6}
      style={{ marginBottom: "20px", maxWidth: "600px" }}
    >
      <DealPreview deal={deal} />
    </Col>
  ));

  return (
    <div style={{ backgroundColor: "black" }}>
      <Container fluid>
        <Row>
          <Col sm={12}>
            <center>
              <h1
                style={{
                  color: "white",
                  textAlign: "center",
                  fontFamily: '"Bodoni 175"',
                  paddingBottom: "28px",
                }}
              >
                Marketplace
              </h1>
            </center>
          </Col>
        </Row>
        <Container>
          <Row>{dealPreviews}</Row>
        </Container>
      </Container>
    </div>
  );
}

export default Marketplace;
