import React, { useEffect, useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import ReactGa from "react-ga";
import * as Sentry from "@sentry/react";

import Tabs from "./Tabs";
import ExecutiveSummary from "./Tabs/ExecutiveSummary";
import DealSummary from "./Tabs/DealSummary";
import SourcesAndUses from "./Tabs/SourcesAndUses";
import ProformaAndReturns from "./Tabs/ProformaAndReturns";
// import DemandDrivers from "./Tabs/DemandDrivers";
// import MarketAndComparables from "./Tabs/MarketAndComparables";
import MediaAndFAQ from "./Tabs/MediaAndFAQ";
// import Documents from "./Tabs/Documents";

function Deal(props) {
  const [deal, setDeal] = useState();

  const fetchDeal = async () => {
    const request = new Request(`/api/v1/deals/${props.match.params.dealId}`, {
      headers: {
        Accept: "application/json",
      },
      method: "GET",
    });

    var response;
    try {
      response = await fetch(request);
    } catch (error) {
      Sentry.captureException(error);
      alert("Please try again");
      return;
    }

    if (response.status === 401) {
      window.location.href = "/login";
    } else if (response.ok) {
      const deal = await response.json();
      setDeal(deal);
    }
  };
  useEffect(() => {
    ReactGa.pageview(window.location.pathname);
    fetchDeal();
  }, []);

  if (deal === undefined) {
    return <div />;
  }

  /*const cookies = new Cookies();
  const user = cookies.get("user", { path: "/" });
  const options = {
    animationEnabled: true,
    exportEnabled: true,
    theme: "dark2", // "light1", "dark1", "dark2"
    title: {
      text: "Trip Expenses",
    },
    data: [
      {
        type: "pie",
        indexLabel: "{label}: {y}%",
        startAngle: -90,
        dataPoints: [
          { y: 20, label: "Airfare" },
          { y: 24, label: "Food & Drinks" },
          { y: 20, label: "Accomodation" },
          { y: 14, label: "Transportation" },
          { y: 12, label: "Activities" },
          { y: 10, label: "Misc" },
        ],
      },
    ],
  };*/

  return (
    <Container>
      <Row>
        <Col>
          <Tabs>
            <div label="Executive Summary">
              <ExecutiveSummary deal={deal} />
            </div>
            <div label="Deal Summary">
              <DealSummary deal={deal} />
            </div>
            <div label="Sources and Uses">
              <SourcesAndUses deal={deal} />
            </div>
            <div label="Proforma and Returns">
              <ProformaAndReturns deal={deal} />
            </div>
            {/* <div label="Demand Drivers">
              <DemandDrivers deal={deal} />
            </div> 
            <div label="Market and Comparables">
              <MarketAndComparables deal={deal} />
            </div>*/}
            <div label="FAQ">
              <MediaAndFAQ deal={deal} />
            </div>
            {/* <div label="Documents">
              <Documents />
            </div> */}
          </Tabs>
        </Col>
      </Row>
    </Container>
  );
}

export default Deal;
